@mixin absolute-position($top: 0, $right: 0, $bottom: 0, $left: 0) {
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

// box-shadow based on https://materializecss.com/shadow.html
@mixin depth($level: 0) {
  @if $level == 1 {
    box-shadow: 0 2px 2px 0 rgba(#000, 0.14), 0 3px 1px -2px rgba(#000, 0.12),
      0 1px 5px 0 rgba(#000, 0.2);
  } @else if $level == 2 {
    box-shadow: 0 4px 5px 0 rgba(#000, 0.14), 0 1px 10px 0 rgba(#000, 0.12),
      0 2px 4px -1px rgba(#000, 0.3);
  } @else if $level == 3 {
    box-shadow: 0 8px 17px 2px rgba(#000, 0.14), 0 3px 14px 2px rgba(#000, 0.12),
      0 5px 5px -3px rgba(#000, 0.2);
  } @else if $level == 4 {
    box-shadow: 0 16px 24px 2px rgba(#000, 0.14), 0 6px 30px 5px rgba(#000, 0.12),
      0 8px 10px -7px rgba(#000, 0.2);
  } @else if $level == 5 {
    box-shadow: 0 24px 38px 3px rgba(#000, 0.14), 0 9px 46px 8px rgba(#000, 0.12),
      0 11px 15px -7px rgba(#000, 0.2);
  }
}
